<template>
  <div>
    <form class="form text-sm" v-on:submit.prevent="submit">
      <div class="box">
        <div class="box-body clearfix form-input-sm">
          <div class="row m-l-xs m-r-xs">
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.username_q}"
              >{{ $t('common.username') }}
              </label>
              <input
                v-model="query.username_q"
                class="form-control w-sm"
                :placeholder="$t('common.username')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.nickname_q}"
              >{{ $t('common.nickname') }}
              </label>
              <input
                v-model="query.nickname_q"
                class="form-control w-sm"
                :placeholder="$t('common.nickname')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': query.sender_ip_q}"
              >{{ $t('common.ip') }}
              </label>
              <input
                v-model="query.sender_ip_q"
                class="form-control w-sm"
                :placeholder="$t('common.ip')"
                @input="search"
              />
            </div>
            <div class="pull-left m-r-xs">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': created_at && (created_at[0] || created_at[1])}"
              >{{ $t('common.created_at') }}
              </label>
              <date-picker
                width='227'
                style="display: block;"
                :not-after="today"
                :shortcuts="shortcuts"
                :inputClass="'mx-input form-control'"
                type="date"
                v-model="created_at"
                format="yyyy-MM-dd"
                range
              />
            </div>
            <button
              class="md-btn pull-right btn m-t-md w-xs"
              type="button"
              @click="clearAll"
              :disabled="isQueryEmpty"
            >
              <span>
                <i class="fa fa-trash-o"></i>
                 {{ $t('action.clear') }}
              </span>
            </button>
            <button
              class="md-btn blue pull-right btn m-r-xs m-t-md w-xs"
              type="button"
              @click="refresh"
            >
              <span>
                <i class="fa fa-spin fa-spinner" v-if="loading"></i>
                <i class="fa fa-repeat" v-else></i>
                 {{ $t('common.refresh') }}
              </span>
            </button>
          </div>
        </div>
      </div>
    </form>

    <div class="box">
      <table st-table="rowCollectionBasic" class="table table-striped">
        <thead>
          <tr>
            <th style="width: 10%;">{{ $t('common.username') }}</th>
            <th style="width: 10%;">{{ $t('common.nickname') }}</th>
            <th style="width: 10%;">{{ $t('common.ip') }}</th>
            <th>{{ $t('setting.chat') + $t('common.content') }}</th>
            <th class="text-center" style="width: 10%;">{{ $t('common.created_at') }}</th>
          </tr>
        </thead>
        <tbody v-if="queryset.length">
          <tr
            v-for="element in queryset"
            :key="element.id"
          >
            <td v-if="element.sender">{{ element.sender.is_robot ? $t('setting.robot') : (element.sender.username || '-') }}</td>
            <td v-else>-</td>
            <td>{{ element.sender ? ( element.sender.nickname || '-' ) : '-' }}</td>
            <td>{{ element.sender_ip || '-' }}</td>
            <td  class="text-break-all" v-if="element.type === 0 || element.type === 2" v-html="element.content">
            </td>
            <td v-else-if="element.type === 1">
              <img
                :src="element.content"
                height="60"
              />
            </td>
            <td class="text-center">{{ element.created_at | moment("YYYY-MM-DD HH:mm:ss") }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <pulling
      :api="api"
      :query="query"
      :queryset="queryset"
      @query-data="queryData"
      @query-param="queryParam"
      ref="pulling"
    />
  </div>
</template>
<script>
import DatePicker from 'vue2-datepicker'
import Pulling from '../components/Pulling.vue'
import api from '../api'
import date from '../utils/date'
import $ from '../utils/util'
import _ from 'lodash'
export default {
  data () {
    return {
      api: api.message,
      messages: [],
      query: {},
      queryset: [],
      created_at: ['', ''],
      loading: true,
      today: date.today[0],
      shortcuts: ['today', 'yesterday', 'this_week', 'this_month', 'last_month'].map(element => Object({
        text: this.$t(`common.${element}`),
        start: date[element][0],
        end: date[element][1]
      }))
    }
  },
  watch: {
    '$route': {
      handler () {
        this.loading = true
        this.setQueryAll()
        this.queryset = []
        this.$refs.pulling.rebase()
      },
      deep: true
    },
    created_at (newObj) {
      [this.query.created_at_0, this.query.created_at_1] = [...newObj]
      this.submit()
    }
  },
  computed: {
    isQueryEmpty () {
      return $.compareQuery(this.query, {})
    }
  },
  created () {
    this.setQueryAll()
    this.$nextTick(() => {
      this.$refs.pulling.rebase()
    })
  },
  methods: {
    setQueryAll () {
      if (this.$route.query.created_at_0 || this.$route.query.created_at_1) {
        this.created_at = [this.$route.query.created_at_0, this.$route.query.created_at_1]
      } else {
        this.created_at = [undefined, undefined]
      }
      this.query = Object.assign({}, this.$route.query)
    },
    queryData (queryset) {
      this.loading = false
      this.queryset = queryset
      this.queryset.forEach(element => {
        if (element.type === 0 || element.type === 2) {
          element.content = element.content.replace(/(\r\n|\r|\n)/g, '<br/>')
        }
      })
    },
    queryParam (query) {
      this.query = Object.assign(this.query, query)
    },
    search:
      _.debounce(function () {
        this.submit()
      },
      700),
    submit () {
      if (!$.compareQuery(this.query, this.$route.query)) {
        this.$refs.pulling.submit()
      }
    },
    clearAll () {
      this.query = {}
      this.$nextTick(() => {
        this.submit()
      })
    },
    refresh () {
      this.loading = true
      this.$nextTick(() => {
        this.$refs.pulling.rebase()
      })
    }
  },
  components: {
    DatePicker,
    Pulling
  }
}
</script>



